@import url('https://fonts.googleapis.com/css?family=Alfa+Slab+One|VT323|Oswald');
body,html{
 background-image: url('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1502013028003&di=57e1ebab5ef62ea71e9f7af6d3651f44&imgtype=0&src=http%3A%2F%2Fb.hiphotos.baidu.com%2Fexp%2Fwhcrop%3D160%2C120%2Fsign%3Dd83863e1d0a20cf446c5a89d19797607%2Fd62a6059252dd42aa98ee5cd033b5bb5c8eab8d5.jpg');
}
.clickable{
  pointer-events : auto;
  cursor:pointer;
}

.unclickable{
  pointer-events:none;
}

.center {
    width: 220px;
    height: 220px;
    position: absolute;
    border-radius: 100%;
    top: 50%;
    left: 50%;
    margin: -122px 0 0 -122px;
    background-color: #ECE7EE;
    border: 12px solid #333;
}

#board{
  width: 472px;
  height: 472px;
  border-radius: 100%;
  position: relative;
  text-align: center;
  margin: auto;
  background-color: #5C4772;
  top: 10vh;
  -webkit-box-shadow: 0px 0px 12px #222;
          box-shadow: 0px 0px 12px #222;
  padding-top:12px;
  padding-left: 5px;
  padding-right:0;
}

.block{
  position: relative;
  width: 220px;
  height: 220px;
  background-color: grey;
  border: 12px solid #003366;
  display: inline-block;
}
#block-0{border-top-left-radius: 100%;}
#block-1{border-top-right-radius: 100%;}
#block-2{border-bottom-left-radius: 100%;}
#block-3{border-bottom-right-radius: 100%;}
#boardCenter{
  width: 220px;
  height: 220px;
  position: absolute;
  border-radius: 100%;
  top: 50%;
  left: 50%;
  margin: -112px 0 0 -112px;
  background-color: #ECE7EE;
  border: 12px solid #333;
}
.brand{
  font-family: 'Alfa Slab One', cursive;
  color: #222;
  font-size: 3.5em;
  margin-top: 35px;
}
.inline{display: inline-block;}
#countNum{font-size:25px;width:50px;height:50px;border-radius: 5px;background-color: brown;line-height: 50px;color:red;}
#countNum.led-off{color:brown;}
#startBtn{width:25px;height:25px;border-radius: 100%;background-color:red;border: 4px solid #444;
-webkit-box-shadow: 0px 2px 3px #222;
        box-shadow: 0px 2px 3px #222;}
#startBtn.btnOn{background-color: red;}
#strictBtn{width:25px;height:25px;border-radius: 100%;background-color: yellow;border: 4px solid #444;
-webkit-box-shadow: 0px 2px 3px #222;
        box-shadow: 0px 2px 3px #222;}
#boardCenter .inline div#strictLight{width:8px;height:8px;border-radius: 100%;background-color: yellow;border: 1px solid #333;
-webkit-box-shadow: 0px 2px 3px #222;
        box-shadow: 0px 2px 3px #222;left:0;}
#boardCenter h3{font-size: 20px;}
#boardCenter .inline{margin:0 8px;}
#boardCenter .inline div{position: relative;left: 9px;}
#power{margin-top: 15px;color:silver;font-weight: bold;}
#switchWrapper{width: 60px;height:20px;position: relative; background-color: #000;border-radius: 5px;cursor: pointer;}
#switchWrapper #switch{position:absolute;left: 4px;height:20px;width:20px;background-color: #3193DE;border-radius: 5px;border:4px solid #333; }
#switchWrapper #switch.on{right:0;left: auto;}
#boardCenter .inline div#strictLight.strictOn{background-color: red;}
#boardCenter .inline div#strictLight.strictOff{background-color: brown;}



